.tab-wrapper {
	margin-left: 2%;
	@include r(991) {
		margin-left: 0; } }
.tabs__item {
	@include clr;
	position: relative; }
.tabs__photo {
	float: left;
	@include cover;
	width: 692px;
	height: 448px;
	@include r(1199) {
		width: 600px;
		height: 400px; }
	@include r(991) {
		width: 400px;
		height: 230px; }
	@include r(767) {
		width: 280px;
		height: 130px;
		float: none;
		margin: 0 auto; } }
.tabs__descr {
	margin-left: 712px;
	padding-right: 17px;
	@include r(1199) {
		margin-left: 625px; }
	@include r(991) {
		margin-left: 415px; }
	@include r(767) {
		margin-left: 0;
		margin-top: 10px; }
	//display: inline-block
	// float: right
	//width: 35%
	//margin-right: 3%
	//float: right
	//не понимаю почему не работает если тут
 }	//включить float: right
.tab_h3 {
	@include r(767) {
		text-align: center; } }
.tabs__price {
	color: #7e7d7d;
	font: 700 20px/1em $sans;
	font-style: italic;
	text-align: right;
	display: block;
	margin-top: 35px;
	@include r(991) {
		font-size: 16px; }
	@include r(767) {
		text-align: center;
		margin-top: 10px; } }
.tabs__article {
	margin-top: 50px;
	position: relative;
	&:before, &:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 1px;
		background: $gray;
		left: 0;
		opacity: 0.3; }
	&:before {
		top: -20px;
		@include r(767) {
			top: -4px; } }
	&:after {
		bottom: -20px;
		@include r(767) {
			bottom: -4px; } }
	@include r(767) {
		margin-top: 10px; } }
.tabs__links {
	display: inline-block;
	position: absolute;
	bottom: 17px;
	right: 19px;
	@include r(1199) {
		position: relative;
		bottom: 0;
		right: 0;
		display: block;
		text-align: right;
		margin-top: 20px; }
	@include r(767) {
		text-align: center;
		margin-top: 10px; }
	a {
		display: inline-block;
		vertical-align: middle;
		margin-right: 10px;
		opacity: 0.5;
		transition: opacity .3s;
		@include rmin(1200) {
			&:hover {
				opacity: 1; } }
		&:nth-of-type(1) {
			@include s(f2); }
		&:nth-of-type(2) {
			@include s(tw2); }
		&:nth-of-type(3) {
			@include s(p); }
		&:nth-of-type(4) {
			@include s(letter); }
		&:nth-of-type(5) {
			@include s(print); } } }
.tabs-nav__item {
	display: inline-block;
	margin-right: 5px;
	@include r(1199) {
		margin-bottom: 5px; }
	a {
		width: 180px;
		height: 117px;
		@include cover;
		display: block;
		@include r(1199) {
			width: 150px;
			height: 90px; }
		@include r(767) {
			width: 100px;
			height: 60px; } } }
.tabs-nav {
	margin-top: 10px;
	@include r(767) {
		text-align: center;
		margin-top: 10px; } }
// gallery style begin
.gallery__nav {
	text-align: center;
	margin-bottom: 47px; }
.gallery__nav-item {
	transition: all .3s;
	display: inline-block;
	margin-right: 7px;
	&:last-of-type {
		margin-right: 0; }
	@include r(767) {
		margin: 0 auto 10px!important;
		display: block;
		width: 280px; }
	a {
		display: block;
		min-width: 168px;
		height: 38px;
		border: 1px solid $orange;
		color: #000;
		font: 400 16px "Open Sans", sans-serif;
		text-transform: uppercase;
		padding: 6px 15px; } }
.ui-tabs-active {
	background: $orange;
	a {
		color: #fff; } }
.gallery__tabs-item {
	//text-align: justify
	text-align: center; }
.gallery-block__img {
	width: 328px;
	height: 212px;
	@include r(1450) {
		width: 280px;
		height: 180px; }
	@include r(991) {
		width: 200px;
		height: 130px; }
	@include r(767) {
		width: 130px;
		height: 60px; } }
.gallery-block {
	position: relative;
	width: 328px;
	height: 212px;
	display: inline-block;
	//margin: 0 5px 44px 0
	margin: 0 4% 44px 0;
	&:nth-of-type(3n+3) {
		margin: 0 0% 44px 0; }
	@include r(1450) {
		margin: 0 70px 44px 0;
		width: 280px;
		height: 180px; }
	@include r(1200) {
		margin: 0 30px 44px 0; }
	@include r(991) {
		margin: 0!important;
		width: 200px;
		height: 130px; }
	@include r(767) {
		margin: 0 auto 5px!important;
		width: 130px;
		height: 60px; }
	a {
		display: block; } }
.gallery-block__desc {
	display: none;
	background: green;
	opacity: 0;
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0; }
