* {
	box-sizing: border-box; }
.out {
	@include clr; }
a {
	text-decoration: none; }
.logo {
	display: inline-block;
	width: 100px;
	height: 100px;
	@include cover;
	@include r(991) {
		width: 70px;
		height: 70px; } }
.h1 {
	color: $green-dark;
	font: 400 22px/0.9em 'Ultra', serif;
	margin-top: 20px;
	display: inline-block;
	text-align: center;
	text-transform: uppercase;
	position: relative;
	&:before, &:after {
		@include s(star);
		content: '';
		position: absolute;
		top: 3px; }
	&:before {
		left: -20px; }
	&:after {
		right: -20px; }
	@include r(991) {
		font: 400 14px/0.9em 'Ultra', serif;
		&:before, &:after {
			display: none; } }
	span {
		font: 700 16px/1.25em $sans;
		color: $blue-light; } }
.h2 {
	color: $main-text-color;
	font: $h2-font;
	position: relative;
	text-transform: uppercase;
	text-align: center;
	&:after {
		content: '';
		position: absolute;
		bottom: -44px;
		left: 50%;
		transform: translateX(-50%);
		width: 45px;
		height: 1px;
		background: #bcc0bd; }
	@include rmin(1366) {
		letter-spacing: 1px; }
	@include r(767) {
		font: $h2-font-mobile; }
	span {
		font-size: 20px;
		display: block;
		@include r(767) {
			font-size: 16px; } } }
.h3 {
	color: $blue;
	font: 400 30px/0.6em $sans;
	@include r(767) {
		font-size: 16px;
		line-height: 1em; } }
.logo2 {
	width: 156px;
	height: 91px;
	display: inline-block;
	@include cover; }
.socials {
	display: inline-block;
	position: relative;
	a {
		display: inline-block; }
	.tw {
		@include s(tw);
		margin-right: 7px; }
	.f {
		@include s(f); } }
.contact-phone, .contact-mail {
	color: #57615a;
	font: $main-text-font; }
.contact-phone {
	margin-bottom: 10px; }
.article {
	color: $main-text-color;
	font: $main-text-font;
	text-align: left;
	@include r(767) {
		font: $main-text-font-mobile; } }
.youtube-block__inner {
	text-align: center;
	display: block;
	opacity: 0.8;
	background-color: #2c7a5c;
	position: relative;
	width: 100%;
	height: 100%;
	color: #fff;
	padding-top: 102px;
	&:before {
		content: '';
		position: absolute;
		top: 29%;
		left: 50%;
		transform: translate(-50%, -50%);
		@include arr(29, 33, #fff, r); }
	&:after {
		content: '';
		@include vcenter;
		width: 95%;
		height: 93%;
		border: 2px solid #fff; }
	@include rmin(1100) {
		opacity: 0.5;
		transition: all .4s; }
	@include r(991) {
		padding-top: 75px; }
	>span {
		font: 400 16px/1.25em $sans;
		display: block;
		margin-bottom: 17px; }
	strong {
		font: 600 21px/0.6em $sans;
		letter-spacing: 16px;
		position: relative;
		right: -8px;
		@include r(767) {
			right: 0;
			letter-spacing: 0; }
		span {
			font-size: 15px;
			display: block; } } }
.youtube-block {
	width: 360px;
	height: 210px;
	position: relative;
	text-transform: uppercase;
	@include cover;
	@include rmin(1100) {
		&:hover {
			.youtube-block__inner {
				opacity: 0.8; } } }
	@include r(991) {
		width: 270px;
		height: 170px; } }
.btn {
	color: #fff;
	text-transform: uppercase;
	font: 400 19px "Open Sans", sans-serif;
	text-align: center;
	padding: 9px 15px;
	background-color: #a84d25;
	min-width: 132px;
	position: relative;
	@include rmin(1200) {

		&:after {
			transition: all .4s;
			@include vcenter;
			width: 100%;
			height: 100%;
			content: '';
			border: 3px solid  #a84d25; }
		&:hover {
			&:after {
				width: 107%;
				height: 120%; } } } }

