.articles {
	padding-top: 70px;
	@include r(767) {
		padding-top: 30px; } }
.articles__h2 {
	margin-bottom: 102px;
	&:after {
		display: none; }
	@include r(767) {
		margin-bottom: 20px; } }
.article-block {
	@include clr;
	margin-left: 3%;
	margin-bottom: 96px;
	display: flex;
	// тут сделал флекс, потому что на мобильных
	// меняю местами картинку с текстом во втором блоке
	&:last-of-type {
		margin-bottom: 0; }
	@include r(1365) {
		margin-left: 0;
		margin-bottom: 40px; }
	@include r(767) {
		flex-wrap: wrap;
		justify-content: center;
		&:nth-of-type(even) {
			.article-block__text {
				order: 1; }
			.article-block__img {
				order: 2; } } } }
.article-block__text {
	//width: calc(100% - 470px)
	//display: inline-block
	//float: left
	//margin-right: 447px
	// /vertical-align: top
	font: $main-text-font;
	color: $main-text-color;
	&:nth-of-type(odd) {
		padding-right: 8%;
		@include r(991) {
			padding-right: 2%; } }
	&:nth-of-type(even) {
		padding-left: 8%;
		@include r(991) {
			padding-left: 2%; } }
	@include r(991) {
 }		//width: calc(100% - 320px)
	@include r(767) {
		font: $main-text-font-mobile;
		padding: 0!important; } }
.article-block__h3 {
	text-transform: uppercase;
	color: #82a997;
	margin-bottom: 25px;
	@include r(767) {
		text-align: center; } }
.article-block__img {
	//float: right
	flex-basis: 448px;
	flex-shrink: 0;
	height: 297px;
	@include r(991) {
		flex-basis: 250px;
		height: 200px; }
	@include r(767) {
		margin-top: 20px;
		flex-basis: 280px;
		height: 140px; } }
