$icons: (0:0);
$icons: map-merge($icons,('f': (X: -139px, Y:0px, W: 7px, H: 15px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('f2': (X: -108px, Y:-26px, W: 11px, H: 22px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
