$icons: map-merge($icons,('facebook@2x': (X: 0px, Y:-47px, W: 20px, H: 38px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('letter': (X: -68px, Y:0px, W: 30px, H: 18px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('next': (X: 0px, Y:0px, W: 24px, H: 37px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('p': (X: -68px, Y:-28px, W: 23px, H: 23px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('prev': (X: -34px, Y:0px, W: 24px, H: 37px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('print': (X: -30px, Y:-47px, W: 26px, H: 26px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('star': (X: -24px, Y:-95px, W: 12px, H: 11px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('submit': (X: -108px, Y:0px, W: 21px, H: 16px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('tw': (X: 0px, Y:-95px, W: 14px, H: 13px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('tw2': (X: -68px, Y:-61px, W: 23px, H: 20px, TW: 146px, TH: 108px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

