.slide {
	height: 767px;
	@include rmin(1501) {
		height: 1000px; }
	@include cover;
	@include r(1365) {
		height: 650px; }
	@include r(991) {
		height: 500px; }
	@include r(767) {
		height: 400px; }
	@include r(550) {
		height: 300px; }
	@include r(400) {
		height: 200px; } }
// как тут правильно было сделать,
