.slick-arrow {
	width: 24px;
	height: 37px;
	z-index: 222;
	top: 43%;
	&:before {
		@include coverdiv;
		content: ''; }
	&:focus, &:hover {
		&:before {
			opacity: 0.75; } }
	&:active {
		&:before {
			opacity: 1; } } }
.slick-prev {
	left: 25px;
	&:before {
		@include s(prev); } }
.slick-next {
	right: 25px;
	&:before {
		@include s(next); } }
